import { Component, ElementRef, EventEmitter, Input, OnInit, Output, ViewChild } from '@angular/core';
import { LocalStorageServiceService } from 'src/service/local-storage-service.service';
import { GetDataBackendService } from 'src/service/get-data-backend.service';
import { AlertController } from '@ionic/angular';
import { Style } from '@capacitor/status-bar';

@Component({
  selector: 'app-Servicios',
  templateUrl: './Servicios.page.html',
  styleUrls: ['./Servicios.page.scss'],
})
export class ServiciosPage implements OnInit {
  @ViewChild('fileInp') fileInput: ElementRef;
  @Input() label: string;
  @Output() data = new EventEmitter<FormData>();

  ButtonAtras: boolean = true
  ShowModal: boolean = false
  DatosUser: any
  ListServicios: any = []
  ServiceSelect: any
  TipoProceso: string
  DatosServi: any = {
    idService: "",
    idUser: "",
    Nombre: "",
    Descripcion: "",
    Monto: "",
    Img: "",
    TipoServicio: ""
  }

  ListaServicios: any = [//Imagenes de los servicios
    {
      Nombre: "Corte de Cabello",
      UrlImg: "/assets/img/Servis_Cabello.jpg"
    },
    {
      Nombre: "Barba",
      UrlImg: "/assets/img/Servis_Barba.jpg"
    },
    {
      Nombre: "Mascarilla",
      UrlImg: "/assets/img/Servis_Facial.jpg"
    },
    {
      Nombre: "Tintes",
      UrlImg: "/assets/img/Servis_Tintes.jpg"
    },
    {
      Nombre: "Productos",
      UrlImg: "/assets/img/Servis_Producto.jpg"
    },
  ]

  constructor(
    private _localstorage: LocalStorageServiceService,
    private _GetData: GetDataBackendService,
    private alertController: AlertController
  ) { }

  ngOnInit() {
    this.DatosUser = JSON.parse(this._localstorage.BuscarItem('DatosUser')) //asigno los datos del usuario que tengo en el local storage 
    this.GetService()
  }

  ViewModal() {
    //muestra u oculto el modal de registrar servicio
    if (this.ShowModal) {
      this.ShowModal = false
    } else if (!this.ShowModal) {
      this.ShowModal = true
    }

  }

  GetService() {
    try {
      //obtengo todos los servicios
      this._GetData.GetServicios(this.DatosUser.idBarbero || this.DatosUser.idUser).then((ResServicios: any) => {
        this.ListServicios = ResServicios
      }).catch(err => console.error(err))

    } catch (error) {
      console.error(error)
    }
  }

  GuardarServi() {
    try {
      //Guardo los datos de los servicios 
      const TipoServiceSelect = this.ListaServicios.find((LS: any) => LS.Nombre === this.DatosServi.TipoServicio)

      TipoServiceSelect ? this.DatosServi.Img = TipoServiceSelect.UrlImg : this.DatosServi.Img = this.DatosServi.Img

      if (this.TipoProceso === 'Actualizar') {
        //Aqui guardo el update que le hizo el cliente al servicio
        this._GetData.UpdateServicios(this.DatosServi).then((ResUpdate: any) => {
          if (ResUpdate.status === true) {

            for (let index = 0; index < this.ListServicios.length; index++) {
              if (this.ListServicios[index].idService === this.ServiceSelect.idService) {
                this.ListServicios[index] = this.DatosServi
              }
            }

            this.ShowAlert('Edit')
            this.CancelarProceso()

          } else { this.ShowAlert('EditError') }

        }).catch((err: any) => { console.error(err); this.ShowAlert('EditError') })


      } else {
        //Aqui preparo la estructura para el nuevo servicio a guardar 
        this.DatosServi.idService = Math.random().toString(36).substring(2, 8 + 2)
        this.DatosServi.idUser = this.DatosUser.idUser

        this.DatosServi.Img = this.DatosServi.Img

        //guardo el nuevo servicio 
        this._GetData.InsertServicios(this.DatosServi).then((ResInsert: any) => {
          if (ResInsert.status === true) {

            this.ListServicios.push(ResInsert.mesage)

            this.ShowAlert('New')

            this.CancelarProceso()

          } else {
            this.ShowAlert('NewError')
          }

        }).catch((err: any) => { console.error(err); this.ShowAlert('NewError') })

      }


    } catch (error) {
      console.error(error)
    }
  }

  UpdateService(IdService: any) {
    //Busco la data del servicio seleccionado para actualizar 
    this.TipoProceso = 'Actualizar'

    this.ServiceSelect = this.ListServicios.find((S: any) => S.idService === IdService)

    this.DatosServi = this.ServiceSelect

    this.ViewModal()
  }

  async DeleteService(IdService: any) {
    try {
      //elimino el servicio seleccionado 
      const alert = await this.alertController.create({
        header: '¿Usted desea eliminar este servicio?',
        buttons: [
          {
            text: 'Cancelar',
            role: 'cancel',
            cssClass: 'secondary',
          },
          {
            text: 'Confirmar',
            handler: async (data) => {

              this._GetData.DeleteServicios(IdService).then((ResD: any) => {

                if (ResD.status && ResD.mesage != null || undefined) {
                  //filtro todos los Servicios sin incluir la que se elimino 
                  this.ListServicios = this.ListServicios.filter((Serv: any) => Serv.idService != IdService)
                  this.ShowAlert('Delete')
                } else {
                  this.ShowAlert('DeleteError')
                }

              }).catch((err: any) => { console.error(err); this.ShowAlert('DeleteError') })

            }
          }
        ]
      });

      await alert.present();

    } catch (error) {
      console.error(error)
    }

  }

  CancelarProceso() {
    //muestro la lista de los servicios y limpio el formulario 
    this.ViewModal()

    this.TipoProceso = ''

    this.DatosServi = {
      idService: "",
      idUser: "",
      Nombre: "",
      Descripcion: "",
      Monto: "",
      Img: "",
      TipoServicio: ""
    }
  }

  async ShowAlert(TipoAlert: String) {
    try {

      const alert = await this.alertController.create({
        header: 'Servicio agregado con éxito',
        buttons: ['Aceptar']
      });

      const alertError = await this.alertController.create({
        header: '¡Hubo un error al guardar el servicio!',
        buttons: ['Aceptar']
      });

      const alertUpdate = await this.alertController.create({
        header: 'El servicio se actualizo con éxito',
        buttons: ['Aceptar']
      });

      const alertErrorUpdate = await this.alertController.create({
        header: '¡Hubo un error al actualizar el servicio!',
        buttons: ['Aceptar']
      });

      const alertDelete = await this.alertController.create({
        header: 'El servicio se elimino con éxito',
        buttons: ['Aceptar']
      });

      const alertErrorDelete = await this.alertController.create({
        header: '¡Hubo un error al eliminar el servicio!',
        buttons: ['Aceptar']
      });

      switch (TipoAlert) {
        case 'New':
          await alert.present();
          break;
        case 'NewError':
          await alertError.present();
          break;
        case 'Edit':
          await alertUpdate.present();
          break;
        case 'EditError':
          await alertErrorUpdate.present();
          break;
        case 'Delete':
          await alertDelete.present();
          break;
        case 'DeleteError':
          await alertErrorDelete.present();
          break;
      }

    } catch (error) {
      console.error(error)
    }
  }

}
